.b-splited {
  position: relative;
  background: white; }
  .b-splited__wrap {
    display: flex;
    width: 100%; }
  .b-splited__block {
    min-height: 100vh;
    flex: 0 0 50%;
    min-width: 0;
    max-width: 50%; }
    .b-splited__block:nth-child(1) .b-splited__content {
      margin: 0 0 0 auto; }
  .b-splited__content {
    height: 100%;
    max-width: 565px;
    padding-left: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center; }
  .b-splited__slider {
    max-width: 50vw;
    max-height: 100vh; }
    .b-splited__slider .item {
      height: 100vh;
      background: center/cover no-repeat; }
  .b-splited__btn-wrap {
    position: absolute;
    text-align: center;
    left: 0;
    right: 0;
    bottom: 47px;
    z-index: 6; }
    .b-splited__btn-wrap .c-btn {
      min-width: 140px; }
